/* This is some Knowledgebas */
.prev-nex-wrap {
	max-width: 1200px;
	margin: 6rem auto;
	.post {
		border: 1px solid var(--c-border);
		border-radius: var(--size-4);
		height: 100%;
		overflow: hidden;
		.featured-image {
			width: 200px;
			min-height: 200px;
			overflow: hidden;
			position: relative;
			img {
				position: absolute;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
		.content-wrap {
			flex-basis: 0;
			flex-grow: 999;
			padding: 1.5rem;
			.title {
				margin-top: 1rem;
			}
		}
		a {
			height: 100%;
			.nav-text {
				font-size: 1rem;
				color: var(--c-text-light);
				display: block;
				margin-bottom: 1rem;
			}
			.title {
				transition: all 250ms;
				display: inline;
				color: var(--c-text-main);
			}
			&:hover {
				text-decoration: none;
				.title {
					text-decoration: underline;
				}
			}
		}
		.post-meta {
			margin-top: 0.5rem;
			font-size: 0.875rem;
			color: var(--c-text-light);
			> :not(:last-child)::after {
				content: '•';
				margin-left: 8px;
				position: absolute;
				display: inline-block;
				font-weight: 700;
			}
			> :not(:first-child) {
				margin-left: 18px;
			}
		}
	}
	.next-post {
		a {
			flex-direction: row-reverse;
		}
		.content-wrap {
			text-align: right;
		}
	}
}
@media screen and (max-width: 991px) {
	.prev-nex-wrap {
		.next-post {
			margin-top: 40px;
			a {
				flex-direction: row;
			}
			.featured-image {
				margin-left: 0;
				margin-right: 24px;
			}
			.content-wrap {
				text-align: left;
			}
		}
	}
}
@media screen and (max-width: 575px) {
	.prev-nex-wrap {
		.post {
			height: auto;
			.featured-image {
				width: 100%;
				height: 250px;
			}
		}
		.next-post {
			.featured-image {
				margin-right: 0;
			}
		}
	}
}
