  transform: translateY(0);
  visibility: visible;
  opacity: 1;
}
.success .notification.success.hidden {
  transform: translateY(-150%);
  visibility: hidden;
  opacity: 0;
}

.error .notification.error {
  transform: translateY(0);
  visibility: visible;
  opacity: 1;
}
