.clearfix::after {
    content: "";
    clear: both;
    display: table;
}
.f-left {
    float: left;
}
.f-right {
    float: right;
}
.text-upper {
    text-transform: uppercase;
}

.no-style-list {
    padding: 0;
    margin: 0;
    list-style: none;
}

// display
.flex {
    display: flex;
    flex-wrap: wrap;
}
.justify-space-between {
    justify-content: space-between;
}
.justify-center {
    justify-content: center;
}
.block {
    display: block;
}
// alignment
.text-center {
    text-align: center;
}
.text-left {
    text-align: left;
}
.text-right {
    text-align: right;
}
.vertical-center > * {
    vertical-align: middle;
}
.object-fit-cover {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0,0,0,0);
    white-space: nowrap;
    border: 0;
}

.lazyload,
.lazyloading {
    opacity: 0;
}
.lazyloaded {
	opacity: 1;
	transition: opacity 0.3s;
}
.grid-sizer {
    visibility: hidden;
    height: 0px;
}
.loading-bg {
    position: relative;
    background: var(--light-bg-color);
    &::after {
        content: "";
        width: 32px;
        height: 32px;
        position: absolute;
        top: calc(50% - 16px);
        left: calc(50% - 16px);
        border: 2px solid var(--border-color);
        border-top-color: transparent;
        border-radius: 50%;
        z-index: 0;
        animation: rotating 0.6s linear infinite;
    }
    &>* {
        z-index: 1;
    }
}
@keyframes rotating {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
}