.pricing-table-wrap {
    margin-bottom: 40px;
}
.pricing-table {
    border: 1px solid var(--c-border);
    padding: 3rem 2rem;
    border-radius: var(--size-4);
    height: 100%;
    flex-direction: column;
    .table-header {
        border-bottom: 1px solid var(--c-border);
        padding-bottom: 2rem;
        position: relative;
    }
    .table-title {
        font-size: 1.5rem;
        font-weight: 700;
        margin-bottom: 8px;
    }
    .price {
        font-size: 4.5rem;
        font-weight: 700;
        line-height: 1.2;
    }
    .term {
        font-weight: 700;
    }
    .table-details {
        flex-grow: 1;
        margin-top: 24px;
        margin-bottom: 40px;
    }
    .label {
        display: inline-block;
        margin-top: 24px;
        background: var(--c-text-main);
        color: var(--c-gray-lighter);
        border-radius: 20px;
        padding: 0 20px;
        line-height: 40px;
        height: 40px;
    }
}

.icon-list {
    list-style: none;
    padding-left: 28px;
    font-size: 1.125rem;
    li {
        margin-bottom: 16px;
        &:before {
            content: "";
            position: relative;
            width: 8px;
            height: 8px;
            margin-left: -24px;
            margin-right: 16px;
            top: -2px;
            line-height: 8px;
            background-color: var(--c-text-main);
            color: var(--c-text-main);
            font-size: 14px;
            font-weight: 700;
            border-radius: 50%;
            justify-content: center;
            display: inline-flex;
            font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        }
    }
}
.currency-name {
    text-transform: uppercase;
}