html {
	box-sizing: border-box;
	font-size: 100%;
}
*, *::before, *::after {
	box-sizing: inherit;
}
body {
	font-family: var(--font-family);
	line-height: 1.6;
	color: var(--c-text-main);
	background-color: var(--c-body-bg);
	-webkit-font-smoothing: subpixel-antialiased;
	-moz-osx-font-smoothing: grayscale;
}
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
	color: var(--text-color-dark);
	font-weight: 700;
	line-height: 1.3;
	margin-bottom: 1rem;
}
h1, .h1 {
	font-size: 3rem; //48px
}
h2, .h2 {
	font-size: 2.5rem; //40px
}
h3, .h3 {
	font-size: 2rem; //32px
}
h4, .h4 {
	font-size: 1.5rem; //24px
}
h5, .h5 {
	font-size: 1.25rem; //20px
}
h6, .h6 {
	font-size: 1rem; //16px
}
.heading-large {
	font-size: 4rem; //64px
}
@media (max-width: 991px) {
	h1, .h1 {
		font-size: 2.75rem; //48px
	}
	h2, .h2 {
		font-size: 2.25rem; //40px
	}
	h3, .h3 {
		font-size: 1.75rem; //32px
	}
	h4, .h4 {
		font-size: 1.25rem; //24px
	}
	h5, .h5 {
		font-size: 1rem; //20px
	}
	h6, .h6 {
		font-size: 1rem; //16px
	}
	.heading-large {
		font-size: 3.75rem; //64px
	}
}
@media (max-width: 767px) {
	h1, .h1 {
		font-size: 2.5rem; //48px
	}
	h2, .h2 {
		font-size: 2rem; //40px
	}
	h3, .h3 {
		font-size: 1.5rem; //32px
	}
	h4, .h4 {
		font-size: 1rem; //24px
	}
	h5, .h5 {
		font-size: 0.875rem; //20px
	}
	h6, .h6 {
		font-size: 0.75rem; //16px
	}
	.heading-large {
		font-size: 2.5rem; //64px
	}
}
a {
	text-decoration: none;
	color: var(--c-theme);
	transition: all 0.25s;
	&:hover {
		text-decoration: underline;
		color: var(--c-theme);
	}
}
figure {
	margin-left: 0;
	margin-right: 0;
}
figcaption {
	font-size: 0.875rem;
	text-align: center;
	margin-top: 8px;
}
img {
	max-width: 100%;
	height: auto;
	display: block;
}

svg {
	fill: currentColor;
	width: 24px;
	height: 24px;
}
hr {
	text-align: center;
	border: none;
	border-bottom: 1px solid var(--c-border);
	margin: 5rem 0;
}
table {
	border-collapse: collapse;
	width: 100%;
	max-width: 100%;
	overflow: auto;
	font-size: 1rem;
}
table,
th,
td {
	border: 1px solid var(--c-border);
}
th {
	color: var(--c-text-main);
}
th,
td {
	padding: 8px 16px;
}
dd,
dt {
	line-height: 24px;
}
dl,
ol,
ul {
	padding-left: 1.3rem;
}
.table-responsive {
	width: 100%;
	display: block;
	overflow-x: auto;
	margin-bottom: 2rem;
	table {
		margin-bottom: 0;
	}
}
pre {
	overflow: auto;
	background-color: var(--c-gray-lighter);
	border-radius: var(--size-2);
	border: 1px solid var(--c-border-light);
	padding: 1rem;
	margin: 2.5rem 0;
	overflow: auto;
	font-size: 1rem;
	line-height: 1.5;
}
code {
	font-family: Consolas, Menlo, Monaco, "Andale Mono WT", "Andale Mono",
		"Lucida Console", "Lucida Sans Typewriter", "DejaVu Sans Mono",
		"Bitstream Vera Sans Mono", "Liberation Mono", "Nimbus Mono L",
		"Courier New", Courier, monospace;
	font-size: 1.125rem;
}
:not(pre) > code {
	font-size: 1.125rem;
	background-color: var(--c-gray-light);
	padding: 2px 8px;
	border-radius: var(--size-1);
}
.btn {
	&:not(:disabled):not(.diabled) {
		cursor: pointer;
	}
	display: inline-block;
	white-space: nowrap;
	text-align: center;
	font-size: 1rem;
	font-weight: 500;
	vertical-align: middle;
	user-select: none;
	padding: 0px 2rem;
	height: 56px;
	line-height: 56px;
	border-radius: 28px;
	border: none;
	background: var(--c-theme);
	color: var(--c-button-text);
	transition: all 0.25s;
	outline: 0;
	align-items: center;
	opacity: 1;
	&:hover,
	&:focus {
		color: var(--c-button-text);
		text-decoration: none;
		opacity: 0.8;
	}
	svg {
		width: 24px;
		height: 24px;
		margin-right: 8px;
	}
}
.btn-sm {
	height: 40px;
	line-height: 40px;
	border-radius: 20px;
	padding: 0 1.5rem;
}
.btn-lg {
	height: 64px;
	line-height: 64px;
	border-radius: 32px;
	font-size: 1.235rem;
}
.btn-block {
	width: 100%;
}
input[type="text"],
input[type="email"],
input[type="url"],
textarea {
	background: var(--body-bg);
	border: 1px solid var(--c-border);
	box-shadow: none;
	height: 56px;
	width: 100%;
	padding: 0px 2rem;
	margin-bottom: 1.5rem;
	font-size: 1rem;
	font-weight: 500;
	color: var(--c-text-main);
	border-radius: 28px;
	&:focus {
		border: 1px solid var(--c-theme);
		color: var(--c-text-main);
		outline: none;
		box-shadow: none;
	}
	&::placeholder {
		color: var(--c-text-light);
	}
}
textarea {
	height: auto;
	padding: 1rem 2rem;
}
input[type="text"],
input[type="email"],
input[type="url"] {
	&.input-sm {
		height: 40px;
		line-height: 40px;
		border-radius: 20px;
	}
	&.input-lg {
		height: 64px;
		line-height: 64px;
		border-radius: 32px;
		font-size: 1.235rem;
	}
}
.form-field {
	margin-bottom: 1.5rem;
}
.field-group-inline {
	display: flex;
	flex-wrap: wrap;
	.form-field {
		margin: 0 6px;
	}
	.input-field {
		flex: 1 0 250px;
	}
}
@media (max-width: 767px) {
	.field-group-inline {
		.form-field {
			min-width: 100%;
		}
	}
}

blockquote {
	font-size: 1.75rem;
	margin: 2.5rem 0;
	padding: 2rem 2rem 2rem 4rem;
	font-weight: 500;
	position: relative;
	background: var(--c-gray-lighter);
	border-radius: var(--size-4);
	&::before {
		content: "\201C";
		font-size: 6rem;
		font-weight: 700;
		font-family: "Arial", sans-serif;
		position: absolute;
		left: 12px;
		top: -8px;
		opacity: 0.1;
	}
}
.site-wrap {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}
[data-nav="sticky"],
[data-nav="sticky-hide"] {
	&:not(.no-header-footer) {
			.site-wrap {
				margin-top: 10rem;
			}
	}
}
@media (max-width: 767px) {
	[data-nav="sticky"],
	[data-nav="sticky-hide"] {
		.site-wrap {
			margin-top: 8rem;
		}
	}
}
@media (min-width: 768px) {
	.main {
		flex-grow: 1;
		padding: 0 20px;
	}
} 
.no-header-footer {
	.main {
		padding: 0;
	}
}
@keyframes loading {
	to {
		transform: rotate(360deg);
	}
}
%loading_spinner {
	position: relative;
	pointer-events: none;
	&:after {
		content: "";
		box-sizing: border-box;
		position: absolute;
		top: 50%;
		left: calc(50% - 10px);
		width: 20px;
		height: 20px;
		margin-top: -10px;
		border-radius: 50%;
		border: 3px solid var(--c-white);
		border-top-color: transparent;
		animation: loading 0.8s linear infinite;
	}
}
.loading {
	.btn,
	&.btn,
	button,
	&.button,
	input[type="submit"],
	&input[type="submit"] {
		@extend %loading_spinner;
		span {
			visibility: hidden;
		}
	}
}
%success_icon {
	position: relative;
	&::before {
		content: "";
		position: absolute;
		top: 50%;
		left: 50%;
		display: inline-block;
		height: 10px;
		width: 20px;
		border: 3px solid var(--c-white);
		border-top-width: 0;
		border-right-width: 0;
		transform: translate(-50%, -75%) rotate(-45deg);
	}
}
.success {
	.btn,
	&.btn,
	button,
	&.button,
	input[type="submit"],
	&input[type="submit"] {
		@extend %success_icon;
		span {
			visibility: hidden;
		}
	}
}

/* medium zoom fix */
.medium-zoom-overlay {
	z-index: 4000000;
	background-color: var(--c-body-bg) !important;
}
.medium-zoom-image--opened {
	z-index: 4000001;
}
