.site-footer {
	margin-top: 4rem;
	padding: 4rem 0;
	background-color: var(--c-gray-lighter);
	a {
		color: var(--c-text-main);
		&:hover {
			color: var(--c-theme);
			text-decoration: none;
		}
	}
}
@media (min-width: 767px) {
	.site-footer {
		padding-left: 20px;
		padding-right: 20px;
	}
}
.widget-about {
	flex: 1 1 360px;
	margin-right: 4rem;
	max-width: 360px;
}
.footer-logo-wrap {
	margin-bottom: 1.5rem;
	.logo-text {
		font-size: 1.75rem;
		font-weight: 600;
		&:hover,
		&:focus,
		&:active {
			color: var(--c-text-main);
		}
	}
	.logo-img {
		img {
			max-height: var(--logo-footer-height);
		}
	}
}

.social-links-wrap {
	align-items: center;
	margin-top: 2rem;
	.title {
		margin-right: 16px;
	}
}
.secondary-nav {
	flex: 1 1 auto;
	.nav-col {
		min-width: 160px;
		flex: 1 0 160px;
		&:not(:last-child) {
			padding-right: 16px;
		}
	}
	.title {
		margin-top: 0;
		font-weight: 400;
		color: var(--c-text-light);
		font-size: 0.875rem;
	}
	.nav-link {
		margin-bottom: 1rem;
	}
	a {
		font-weight: 500;
	}
}
.footer-bottom {
	margin-top: 3rem;
	font-size: 0.875rem;
	text-align: center;
	> * {
		margin-top: 1rem;
	}
}
.social-links {
	a {
		display: inline-flex;
		margin: 0 16px 0 0;
		text-align: center;
		color: var(--c-text-main);
		svg {
			width: 18px;
			height: 18px;
		}
	}
}
.copyright {
	font-size: 0.875rem;
}

@media screen and (max-width: 991px) {
	.widget-about {
		margin-right: 0;
		max-width: 500px;
	}
	.secondary-nav {
		.nav-col {
			margin-top: 2.5rem;
		}
	}
}
@media screen and (min-width: 992px) {
	.secondary-nav {
		justify-content: flex-end;
		.nav-col {
			max-width: 230px;
		}
	}
}
