.single-post {
	max-width: 1200px;
	margin: 0 auto;
	.tag-wrap {
		a {
			margin: 0 8px 8px 0;
		}
	}
	.post-title {
		margin-top: 1.5rem;
		margin-bottom: 1.5rem;
	}
	.page-title {
		margin-top: 0;
		margin-bottom: 1.5rem;
	}
	.custom-excerpt {
		margin-bottom: 3rem;
		font-size: 1.5rem;
		max-width: 780px;
		color: var(--c-text-light);
	}
	.avatar {
		&.no-image {
			svg {
				color: var(--c-text-main);
				width: 120%;
				height: 120%;
				margin-left: -10%;
				opacity: 0.5;
			}
		}
	}
	.featured-image-wrap {
		margin: 4rem 0 0;
		border-radius: var(--size-4);
		overflow: hidden;
		.featured-image {
			width: 100%;
			height: auto;
		}
	}
	.featured-image-caption {
		margin-top: 8px;
	}
}
.page-template {
	.single-post {
		.custom-excerpt {
			margin-left: auto;
			margin-right: auto;
		}
	}
	.no-image {
		.custom-excerpt {
			margin-top: 4rem;
		}
	}
}
.post-content {
	max-width: 780px;
	margin: 4rem auto 0;
	font-size: 1.25rem;
	font-weight: 400;
	p {
		line-height: 1.7;
	}
	a {
		color: var(--c-text-main);
		text-decoration: underline;
		&:hover {
			color: var(--c-theme);
		}
		&.btn {
			border-bottom: none;
			color: var(--c-white);
			text-decoration: none;
		}
	}
	li {
		margin-bottom: 8px;
	}
	ul ul,
	ol ol {
		margin: 10px 0;
	}
	blockquote {
		line-height: 1.4;
	}
	& > * {
		margin-bottom: 4rem;
	}
	& > h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		margin-bottom: 1rem;
	}
	& > h1 {
		margin-top: 5rem;
	}
	& > h2 {
		margin-top: 4.5rem;
	}
	& > h3 {
		margin-top: 4rem;
	}
	& > h4 {
		margin-top: 3.5rem;
	}
	& > h5 {
		margin-top: 2.5rem;
	}
	& > h6 {
		margin-top: 2rem;
	}
	figure {
		margin-top: 4rem;
		margin-bottom: 4rem;
	}
	// override border style for bookmark card
	.kg-bookmark-container {
		text-decoration: none;
		border: 1px solid var(--c-gray-light);
	}
}
.kg-image-card {
	img {
		border-radius: var(--size-4);
		width: 100%;
	}
}
@media (min-width: 1300px) {
	.kg-width-wide {
		width: calc(100% + (1138px - 100%));
		margin-left: calc((100% - 1138px) / 2);
	}
}
@media (min-width: 768px) and (max-width: 1300px) {
	.kg-width-wide {
		width: calc(100% + (100vw - 100%) / 2);
		margin-left: calc((100% - 100vw) / 4);
	}
}
@media (min-width: 768px) and (max-width: 991px) {
	.kg-width-full {
		width: calc(100% + (100vw - 100%) / 2);
		margin-left: calc((100% - 100vw) / 4);
	}
}
@media (min-width: 992px) {
	.kg-width-full {
		width: calc(100vw - 128px);
		margin-left: calc(50% + 64px - 50vw);
	}
}
.kg-embed-card {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.kg-gallery-container {
	display: flex;
	flex-direction: column;
}
.kg-gallery-row {
	display: flex;
	flex-direction: row;
	justify-content: center;
}
.kg-gallery-image img {
	display: block;
	margin: 0;
	width: 100%;
	height: 100%;
}
.kg-gallery-row:not(:first-of-type) {
	margin: 0.5rem 0 0 0;
}
.kg-gallery-image:not(:first-of-type) {
	margin: 0 0 0 0.5rem;
}
.kg-gallery-row:first-of-type .kg-gallery-image:first-of-type img {
	border-top-left-radius: var(--size-4);
}
.kg-gallery-row:first-of-type .kg-gallery-image:last-of-type img {
	border-top-right-radius: var(--size-4);
}
.kg-gallery-row:last-of-type .kg-gallery-image:first-of-type img {
	border-bottom-left-radius: var(--size-4);
}
.kg-gallery-row:last-of-type .kg-gallery-image:last-of-type img {
	border-bottom-right-radius: var(--size-4);
}
.kg-bookmark-card {
	margin-top: 4rem;
	margin-bottom: 4rem;
}
.kg-bookmark-container {
	display: flex;
	flex-wrap: wrap;
	color: var(--c-text-main);
	text-decoration: none;
	flex-direction: column-reverse;
	border-radius: var(--size-3);
	border: 1px solid var(--c-gray-light);
	background: var(--c-gray-lighter);
	overflow: hidden;
	&:hover {
		color: var(--c-text-main);
		border-color: var(--c-gray-light);
		text-decoration: none;
		.kg-bookmark-title {
			color: var(--c-theme);
		}
		.kg-bookmark-description,
		.kg-bookmark-metadata {
			color: var(--c-text-main);
		}
	}
}
.kg-bookmark-content {
	flex-basis: 0;
	flex-grow: 999;
	min-width: 50%;
	padding: 1.5rem;
}
.kg-bookmark-title {
	line-height: 1.4;
	color: var(--c-text-main);
	transition: all 300ms;
	font-weight: 700;
}
.kg-bookmark-description {
	font-size: 1rem;
	line-height: 1.6;
	display: -webkit-box;
	margin-top: 16px;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	color: var(--c-text-main);
	overflow: hidden;
}
.kg-bookmark-metadata {
	display: flex;
	align-items: center;
	font-size: 0.75rem;
	margin-top: 24px;
	color: var(--c-text-main);
}
.kg-bookmark-thumbnail {
	flex-basis: 216px;
	flex-grow: 1;
	position: relative;
	border-radius: 0 var(--border-radius-large) var(--border-radius-large) 0;
	overflow: hidden;
}
.kg-bookmark-thumbnail img {
	position: absolute;
	top: 0;
	left: 0;
	vertical-align: bottom;
	object-fit: cover;
	width: 100%;
	height: 100%;
}
.kg-bookmark-icon {
	width: 20px;
	height: 20px;
	margin-right: 8px;
	vertical-align: bottom;
}
.kg-bookmark-author,
.kg-bookmark-publisher {
	font-size: 0.875rem;
	font-weight: 700;
}
.kg-bookmark-publisher:before {
	content: '•';
	margin: 0 6px;
}

@media screen and (min-width: 768px) {
	.post-content {
		.kg-bookmark-container {
			flex-direction: row;
		}
		.kg-bookmark-thumbnail {
			flex-basis: 32%;
			min-height: 144px;
		}
	}
}
.post-footer {
	max-width: 780px;
	margin: 0 auto;
}
.share-wrap {
	margin-top: 5rem;
	margin-bottom: 3rem;
}
.share-title {
	margin-bottom: 1.5rem;
}
.share-links {
	justify-content: center;
	a {
		color: var(--c-text-main);
		margin: 0 12px 16px 12px;
		background-color: var(--c-gray-lighter);
		padding: 8px;
		border-radius: 50%;
		width: 40px;
		height: 40px;
		display: flex;
		align-items: center;
		justify-content: center;
		&:hover,
		&:focus,
		&:active {
			background-color: var(--c-theme);
			color: var(--c-white);
		}
		svg {
			width: 16px;
			height: 16px;
		}
	}
}
.js-notification-copy-link {
	opacity: 0;
	transition: opacity 300ms;
	span {
		display: inline-flex;
		margin: 0 auto;
		background: var(--c-success);
		color: var(--c-black);
		font-weight: 500;
		padding: 4px 16px;
		min-height: 32px;
		border-radius: var(--size-3);
	}
	&.visible {
		opacity: 1;
	}
}

.about-author-section-title {
	margin-bottom: 2rem;
}
.about-author {
	padding: 2rem;
	margin-bottom: 2rem;
	background: var(--c-gray-lighter);
	border-radius: var(--size-4);
	.avatar-wrap {
		width: 108px;
		height: 108px;
		border-radius: 50%;
		overflow: hidden;
		margin-right: 1.5rem;
	}
	.avatar {
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
		background: var(--c-gray-light);
		&.no-image {
			svg {
				color: var(--c-text-main);
				width: 120%;
				height: 120%;
				margin-left: -10%;
				opacity: 0.5;
			}
		}
	}
	.author-info {
		flex: 1 0 0px;
	}
	.bio {
		font-size: 1.125rem;
	}
	.name {
		margin-top: 0;
		margin-bottom: 0.75rem;
		a {
			color: var(--c-text-main);
		}
	}
}
@media screen and (max-width: 575px) {
	.about-author {
		flex-direction: column;
		.avatar-wrap {
			margin: 0 auto 1rem;
		}
		.author-info {
			text-align: center;
		}
	}
}

@media screen and (max-width: 768px) {
	.single-post {
		.custom-excerpt {
			font-size: 1.25rem;
		}
	}
	.post-content {
		font-size: 1.125rem;
		margin-bottom: 2rem;
		word-wrap: break-word;
	}
}
